*, *::before, *::after {
  box-sizing: border-box;
}
html,
body {
  height: 100%;;
}
body {
  display: flex;
  margin: 0;
  padding: 0;
  color: #ffffff;
  background: #000000 url(data:image/png;base64,) 0 0 repeat;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex: 1 0 auto;
  background: rgba(#000000, .3);
  background: -webkit-linear-gradient(top, #000000 0%, transparent 50%, #000000 100%);
  background: linear-gradient(to bottom, #000000 0%, transparent 50%, #000000 100%)
}
h1 {
  text-align: center;
}
.panels-wrapper {
  display: flex;
  justify-content: space-between;

  flex-direction: column;
}
.panels {
  display: flex;
  flex-wrap: wrap;
  max-width: 1120px;
}
.panel {
  position: relative;
  width: 320px;
  height: 480px;
  overflow: hidden;
  margin: 20px;
  border-radius: 12px;
  background: #ffffff;
  -webkit-clip-path: polygon(45px 0%, 0% 0%, 100% 0%, 100% 100%, 100% 100%, 0% 100%, 0% 100%, 0% 45px);
          clip-path: polygon(45px 0%, 0% 0%, 100% 0%, 100% 100%, 100% 100%, 0% 100%, 0% 100%, 0% 45px);
}
.panel::after {
    position: absolute;
    top: -34px;
    left: -35px;
    width: 70px;
    height: 70px;
    border-right: solid 4px #e52e7a;
    content: "";
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
.panel-content {
  position: relative;
  width: 100%;
  height: 100%;
  border: solid 4px #e52e7a;
  border-radius: 12px;
}
